/* BASE */

/* Colours */

$gray: #e7e7e7;
$red: #981e32;

/* Font awesome icons */
li
.famr {
margin-right: 10px;
}

/* Links */

#contentContainer a:link, #contentContainer a:visited, #contentContainer a {
color: $red;
text-decoration: none;
}
l
#contentContainer a:hover, #contentContainer a:active, #contentContainer a:hover  { 
text-decoration: underline !important;
}

/* Featured image links */

#contentContainer .columnFull a h2, #contentContainer .columnFull a h3 {
color: $red !important;
}

/* Margins - Padding */

#contentContainer .noPadding {
padding: 0 0 10px 0 !important;
}

#contentContainer .pt10 {
padding-top: 10px;
}

#contentContainer .mt10 {
margin-top: 10px;
}

#contentContainer .mt20 {
margin-top: 20px;
}

#contentContainer .border {
padding-top: 20px;
padding-bottom: 0px;
border-bottom: 2px solid #e7e7e7;
}

#contentContainer .border.noBoxPadding {
padding-bottom: 0;	
}

#contentContainer .border:last-of-type {
border-bottom: none;	
}

#contentContainer .border:first-of-type {
padding-top: 0px;	
}

/* Lists */

.backgroundRed ul.listCaret li:before {
color: white;
}

#contentContainer .columnFull ul.listCaret {
margin-bottom: 0 !important;
}

#contentContainer .columnFull  ul.listCaret li {
  position: relative;
  padding-left: 15px;
  background-image: none;
}

#contentContainer .columnFull  ul.listCaret li::before {
  position: absolute;
  font-family: 'Font Awesome 5 Free';
  top: 0;
  left: 0;
  content: "\f0da";
  font-weight: 900;
}

#contentContainer .columnFull p.moreLink::before {
  position: relative;
  float: left;
  font-family: 'Font Awesome 5 Free';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f061";
  font-weight: 900;
  color: $red;
}

#contentContainer .columnFull p.arrowLeftLink::before {	
  float: left;
  position: relative;
  font-family: 'Font Awesome 5 Free';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f060";
  font-weight: 900;
  color: $red;
}

#contentContainer .columnFull ul.social li.facebook::before {
  float: left;
  position: relative;
  font-family: 'Font Awesome 5 Brands';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f39e";
  font-weight: 900;
  color: $red;
}

#contentContainer .columnFull ul.social li.linkedin::before {
  float: left;
  position: relative;
  font-family: 'Font Awesome 5 Brands';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f0e1";
  font-weight: 900;
  color: $red;
}

#contentContainer .columnFull ul.social li.youtube::before {
  float: left;
  position: relative;
  font-family: 'Font Awesome 5 Brands';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f167";
  font-weight: 900;
  color: $red;
}

#contentContainer .columnFull ul.social li.twitter::before {
  float: left;
  position: relative;
  font-family: 'Font Awesome 5 Brands';
  top: 0;
  left: 0; 
  padding-right: 5px;
  content: "\f099";
  font-weight: 900;
  color: $red;
}


#contentContainer .columnFull .backgroundRed p.moreLink::before {
color: white;
}



ul.mostPopular {
font-size: 1.1em;
}

#contentContainer ol, #contentContainer ul {
padding-left: 0;
margin: 0;
}

#contentContainer ul {
margin: 0 0 15px 10px !important;
}

#contentContainer .columnFull ul.multistep {
margin: 0 0 10px 5px !important;
}

#contentContainer .columnFull ul.listLinks {
margin: 0 0 15px 0px !important;
}

#contentContainer .columnFull ul {
margin: 0 0 15px 0px !important;
}

#contentContainer ol.multistep li {
margin-left: 15px;
padding: 0;
}

#contentContainer ul.multistep li {
margin-left: 10px;
}

#contentContainer ul.linkArrow li {
padding-left: 0;
}

#contentContainer .linkArrow li {
display: block; }

#contentContainer .linkArrow li a {
text-decoration: none !important; }

#contentContainer .linkArrow li a:hover {
text-decoration: underline !important; }



#contentContainer ul.itemThumbBook li {
float: left;
padding: 0 !important;
margin-bottom: 10px;
width: 100%;
}

#contentContainer ul.itemThumbBook li p {
margin: 0 !important;
}

#contentContainer ul.listLinks li
 {
background: none;
padding-left: 0;
list-style-type: none;
margin: 0;
clear: both;
float: left;
}

#contentContainer ul.mostPopular li {
font-weight: 700;
}

#contentContainer .columnFull .column.noBoxPadding ul.listLinks {
margin-bottom: 10px !important;
}

#contentContainer ul.listLinks li a {
float: left;
}

#contentContainer ul.listLinks li span {
display: inline-block;
}

#contentContainer ul.listLinks p {
clear:both;
}

#contentContainer ul.guidanceList li, ul.articleList li {
list-style-type: none;
background: none;
padding: 0;
@media only screen and (min-width : 992px) {
min-height: 133px;	
clear: both;
float: left;
}
}

#contentContainer ul.list li {
list-style-type: none;
background: none;
padding: 0;	
}

#contentContainer .mb0 {
margin-bottom: 0;	
}

/* Main content */

#contentContainer ol {
margin-bottom: 0;
}

#contentContainer .pressReleases li {
padding-bottom: 20px;	
}

#contentContainer .safetyAlerts li {
list-style-type: none;
}


/* Forms */

input#email {
box-sizing:border-box;
padding: 5px;
}

.eBulletinButton {
background: $red;
color: white;
padding: 5px;

}
