/* Beta */


#beta-user-testing {
width: 100%;
float: left;
padding: 10px;
border: 2px solid #005ea5;	
margin-top: 10px;
box-sizing: border-box;
}

.beta-highlight {
display: inline-block;
margin: 0 8px 0 0;
padding: 2px 5px 0;
font-family: "nta", Arial, sans-serif;
font-weight: 700;
text-transform: none;
font-size: 14px;
line-height: 1.1428571429;
text-transform: uppercase;
letter-spacing: 1px;
text-decoration: none;
color: #fff;
background-color: #005ea5;
font-size: 16px;
line-height: 1.25;
}

#beta-user-testing a:link, #beta-user-testing a:visited, #beta-user-testing a:hover, #beta-user-testing a:focus {
color: #005ea5 !important;	
}

#beta-user-testing p {
font-size: 16px;
margin: 0px;
}

/* Global */

.padding10 {
padding: 10px;
box-sizing: border-box;
}

.inlineRight p {
float: left;
display: inline-block;
margin-right: 10px;
} 

#responsiveContent a {
text-decoration: none;	
}

#responsiveContent a:hover {
text-decoration: underline;	
}

.backgroundRed {
background: $red !important;
}

/* More links */

.columnFull.moreLink {
position: relative !important;
}

@media only screen and (min-width : 1170px) {
.moreLink {
padding-bottom: 30px !important;
}
}

.backgroundGray {
background: $gray !important;
}

p.moreLinkSide {
clear: both;	
}

@media only screen and (min-width : 992px) {

p.moreLinkSide {
position: relative;
width: 100%;
padding: 0;
margin: 0;
margin-top: 0;
float: left;
clear: both;
font-weight: 700;
margin-bottom: 20px;
}	
	
}

p.moreLink {
@media only screen and (min-width : 992px) {
position: absolute;
}
bottom: -30px;
height: auto;
clear: both;
font-weight: 700;
}

.noBoxPadding.twoThirdHalf p.moreLink {
bottom: -50px;
}

p.moreLink a {
padding: 0;
}

/* Font awesome */

i.fas {
margin-right: -5px;
padding-right: 10px;
}


/* Side content */

.exploreTopic {
width: 100%;
float: left;
box-sizing: border-box;
padding: 10px;
margin-top: 10px;
}

#contentContainer .topTaskSide .multistep {
width: 100%;
}


.box.no-padding {
padding: 0;
}

.next-page {
float: right;
margin: 0;
}

#contentContainer .next-page ul {
margin: 0;
}

.next-page a, .previous-page a {
padding: 20px;
}
.next-page a {
float: right;
}

.previous-page, .next-page {
width: auto;
}

.next-page li, .previous-page li {
color: #B82533 !important;
list-style-type: none;
}

.next-page a span, a:visited span {
color: #B82533;
}

.next-page a:hover, .previous-page a:hover {
background: #e8e8e8;
}

.previous-page span, .next-page span {
float: left;
display: inline-block;
clear: both;
padding-bottom: 10px;
}

.next-arrow {
font-size: 2.167em;
}

.next-text {
font-size: 1.167em;
}

.next-page a, .previous-page a {
width: 95%;
float: left;
padding: 2.5%;
}

.previous-page {
float: left;
width: 100%;
margin: 0;
}

.next-page {
float: left;
width: 100%;
}

/* Small */

@media only screen and (min-width : 768px) {

.previous-page {
float: left;
width: 50%;
margin: 0;
}

.next-page {
float: left;
width: 50%;
}	

}

/* Homepage */

.homepageBanner {
padding-top: 0;
padding-bottom: 0;
}

/* Small Devices, Tablets */
@media only screen and (max-width : 992px) {
.homepageBanner {
margin-top: 20px;
}
}



.backgroundRed a, .backgroundRed h2, .backgroundRed h3, .backgroundRed p {
color: white !important;
}



/* Responsive carousel */

.touchcarousel-item img {
border-radius: 0 !important;
}

ul.touchcarousel-container {
width: 1200px!important;
}

@media only screen and (min-width : 300px) {

#homepageCarousel {
width: auto;
max-width: 300px !important;
}	
	
		
}

@media only screen and (min-width : 600px) {

#homepageCarousel {
width: auto;
max-width: 540px !important;
}	
	
		
}	

@media only screen and (min-width : 700px) {

#homepageCarousel {
max-width: 640px !important;
}	
	
		
}	

@media only screen and (min-width : 900px) {

#homepageCarousel {
max-width: 840px !important;
}	
	
		
}	

@media only screen and (min-width : 1169px) {

#homepageCarousel {
max-width: 960px !important;
}	
	
	
		
}






/* ES */

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {

.linkArrow li {
display: block;
}	
	
}


/* News */

#contentContainer .listLinks a {
text-decoration: none;
}

#contentContainer .listLinks a:hover {
text-decoration: underline;
}

.subscribe img {
display: inline-block;
float: left;
}

.subscribe h2:before {
  content:url('../../assets/images/icons/emaili.png'); 
  position:relative;
  z-index:100000;
  left:0;
  top: 5px;
  padding-right: 10px;
}

.subscribe .eBulletinButton {
width: auto;
font-size: 1em;
}

.subscribe label {
color: #4E4E4E;
}

.subscribe input[type=submit] {
    padding:5px 15px;
	margin-top: 10px;
    background:#ccc; 
    border:0 none;
    cursor:pointer;
    -webkit-border-radius: 5px;
    border-radius: 5px;
	background: #a61333;
	color: white;
}

/* MODULES */

/* Secondary Search */

#secondarySearch {
width: 100%;
margin-top: 20px;
float: left;
}

#secondarySearch table {
width: 100% !important;
margin: 0;
}

#secondarySearch table td {
border: none;
padding: 0;
}

#secondarySearch table .gsc-input-box {
border: none;
border-color: none;
}

#secondarySearch table .gsc-input {
background: $gray !important;
border: none;
text-indent: 20px !important;
}

#secondarySearch table form.gsc-search-box {
margin-bottom: 0 !important;
}

#secondarySearch table .gsc-search-button .gsc-search-button-v2 {
background: $red;
}

#secondarySearch table .gsc-search-button-v2 svg {
fill: white !important;
}