
#headerContainer {
min-width: 960px;
}

#header {
max-width: 940px;
width: 940px;
padding-left: 0px;
padding-right: 0px;
}
	
#pageContainer {
width: 940px;
float: none;
}
	
#contentContainer {
margin: 0;
width: 940px;
}

#contentContainer .columnFull {
float: left !important;
}

	
/* Layout */
	
#contentContainer .OneCol {
width: 260px;
margin-right: 20px;
}
	
#contentContainer .TwoCol {
width: 640px;		
}
	
#contentContainer .oneThird {
width: 280px;
margin-right: 20px;
}

#contentContainer .twoThird {
width: 600px;
margin-right: 20px;
}
	
#contentContainer .twoThirdHalf {
width: 270px;
margin-right: 20px;
}
	
#contentContainer .twoThirdHalf.subColumn {
width: 290px;
padding: 0;
}
	
#contentContainer .noBoxPadding .twoThirdHalf.subColumn {
width: 300px;
}
	
#contentContainer .noBoxPadding.twoThirdHalf {
width: 290px;
}
	
.multistep {
width: 280px;
float: left;
}
	
/* No box padding */
	
#contentContainer .noBoxPadding.oneThird {
width: 300px;
margin-right: 20px;
}
	
#contentContainer .noBoxPadding .twoThird {
width: 620px;
margin-right: 20px;
}

#contentContainer .noBoxPadding .multistep {
width: 305px;
float: left;
}
	
/* Borders */
	
#contentContainer .border {
padding-top: 20px;
padding-bottom: 20px;
border-bottom: 2px solid #e7e7e7;
}

	
/* Beta */

#beta-user-testing {
width: 940px;
}

#globalSearch {
float: right;	
}
#globalSearch table {
width: 300px;	
}


.beta-highlight {
margin-left: 0;
}

#contentContainer.column1 {
width: 940px;
}

#contentContainer {
margin: 7px 0 0;
}

#contentContainer ol {
margin: 0 0 1em .1em;
}

#off-canvas-container {
display: none;
}

.offCanvasHeaderLogo {
display: none;
}

	
/* Layout */

.oneHalfContent {
box-sizing: border-box;
width: 310px;
margin-left: 0;
margin-right: 0;
}

.oneQuarterContent {
width: 135px;
margin-left: 10px;
margin-right: 10px;	
}	