


/* Layout borders */

#contentContainer .border-top {
border-top: 2px solid #e8e8e8;
border-radius: 0;
}

#contentContainer .border-top-red {
border-top: 2px solid #A70632;
border-radius: 0;
} 

#contentContainer .border-right {
border-right: 2px solid $gray;
}



/* Content types */

.last {
margin-right: 0 !important;
}

.noBoxPadding .column {
padding-left: 0 !important;
padding-right: 0 !important;
}

.noBoxPadding.column {
padding-left: 0;
padding-right: 0;
}



.column {
float: left;
position: relative;
padding: 10px;
}

#beta-user-testing {
width: 100%;
}



